<template>
<div class="question-page">
  <div class="header">
    <div class="logo">
      <img src="../assets/answer_logo.png">
      <div class="split-vertical">
      </div>
      <span>
        法律咨询
      </span>
    </div>
  </div>

  <div class="answer-question">
    <transition
      :name="transitionName"
      @after-leave="afterLeave"
      @before-enter="beforeEnter"
      @enter="enter"
      @after-enter="afterEnter"
      @before-leave="beforeLeave"
      @leave="leave">
      <div v-if='transitionFrame === 0' class="question-row prev" :class="{'report-row': uiType === 4}">
        <xSpin class="loading-spinner" v-model="loading" />
        <!-- 首页 -->
        <template v-if="uiType === 1">
          <div class="questionnaire">
            <!-- 标题 -->
            <div class="project-title">
              {{questionnaire.title}}
            </div>
            <!-- 说明 -->
            <div class="project-description">
              <span class="description">
                {{questionnaire.description || '为了方便对您的情况进行详细分析，请根据问卷提示填写相关信息，作答结束后，在线为您出具专业法律咨询意见书，供您参考。'}}
              </span>
            </div>
            <div class="bottom-btn">
              <button class="start-btn" @click="start">开始咨询</button>
            </div>
          </div>
        </template>

        <!-- 题目页面 -->
        <template v-if="uiType === 2">
          <div class="go-last" :class="{'show': showLastBtn}" @click.stop="answer(2)">
            <img src="../assets/last.png">
            <span class="text">上一题</span>
          </div>
          <div class="redo" @mouseover="showLastBtn = false" @mouseout="showLastBtn = true" @click.stop="reset($event);showLastBtn = true">
            <img src="../assets/redo.png">
            <span class="text">重做</span>
          </div>
          <div class="questionnaire" v-if="!report && this.schema">
            <div class="questions">
              <div style="height: 100%;display: flex;flex-direction: column;">
                <h2 class="question-title" style="text-align: center; flex: 0 0 auto">
                  {{ schema.title }}
                </h2>
                <p class="question-description">
                  {{ schema.description }}
                </p>
                <div class="question-options">
                  <template v-for="(key, index) in schema.properties">
                    <Additional v-if="key.additional" :title="key.title" v-model="key.additionalShow" :key="index" :additionalKey="index" @commit="Commit" @have="AdditionalDown">
                      <template v-for="(k, inx) in key.properties">
                        <schemaComponent :name="getComponentName(k)" apiType="public" :propsObject="k" :schemaKey="index + '-----' + inx" @input="Input" @commit="Commit" @additional="Additional" :key="inx" @validator="initValidator" />
                      </template>
                    </Additional>
                    <MultiNest v-else-if="key.uuid === '00000000-0000-0000-0000-000000000012' && key.multiKey" :properties="schema.properties" :answers="answers" :title="key.title" :schemaKey="index" :key="index" :multiKey="key.multiKey" @additional="Additional"/>
                    <schemaComponent v-else="" apiType="public" :name="getComponentName(key)" :propsObject="key" :schemaKey="index" @input="Input" @commit="Commit" @additional="Additional" :key="index" @validator="initValidator"/>
                  </template>
                </div>
              </div>
            </div>
          </div>
        </template>

        <!-- 填写信息页面 -->
        <template v-if="uiType === 3">
          <div class="go-last" :class="{'show': showLastBtn}" @click.stop="answer(2)">
            <img src="../assets/last.png">
            <span class="text">上一题</span>
          </div>
          <div class="redo" @mouseover="showLastBtn = false" @mouseout="showLastBtn = true" @click.stop="reset($event);showLastBtn = true">
            <img src="../assets/redo.png">
            <span class="text">重做</span>
          </div>
          <div class="questionnaire commit-info">
            <div class="title">
              <img class="image" src="../assets/commit.png">
              <h2 class="text">提交成功</h2>
            </div>
            <div class="description">
              您的情况已提交成功，正在为您详细分析，请您填写联系方式方便律师及时与您沟通，如有不便，可不填跳过。
            </div>
            <div class="info-form">
              <xInput v-model="customerInfo.name" placeholder="您的称呼（如：王先生"/>
              <xInput v-model="customerInfo.phone" placeholder="您的联系方式"/>
              <button class="lp-btn-primary" @click="getReport">提交并查看法律分析报告</button>
            </div>
          </div>
        </template>

        <!-- 报告页面 -->
        <template v-if="uiType === 4">
          <div class="report">
            <div class="report-title">律品法律咨询意见书</div>
            <div class="report-greetings">
              <h1>先生您好：</h1>
              <p>非常感谢您对我们的信任与支持。以下法律意见是根据你填写的信息，并参考上千万份法院裁判文书数据，以及业内资深法官、律师、法律专业工作者的集体智慧生成。供您参考！</p>
            </div>
            <div class="report-content report-md" ref="report" v-html="reportContent">
            </div>
          </div>
          <div class="side-bar">
            <div class="report-nav">
              <h2>报告导航</h2>
              <ul class="nav-list">
                <li class="nav-item" v-for="(item, index) in outlines" :key="index">
                  <a :href="item.anchor">{{index + 1}} {{item.title}}</a>
                </li>
              </ul>
              <ul class="operation-list">
                <li class="operation-item">
                  <img src="../assets/download.png">
                  <span class="m-l-15">下载报告</span>
                </li>
                <li class="operation-item">
                  <img src="../assets/download.png">
                  <span class="m-l-15">打印报告</span>
                </li>
                <li class="operation-item" @click.stop="reset($event);showLastBtn = true">
                  <img src="../assets/download.png">
                  <span class="m-l-15">重做</span>
                </li>
              </ul>
              <div class="qr-code">
                <div class="qr-img">
                  <img src="../assets/QRCODE.png">
                </div>
                <p class="qr-text">
                  扫描二维码在手机中进行查看
                </p>
              </div>
            </div>
          </div>
        </template>
      </div>
    </transition>
    <transition
      :name="transitionName"
      @after-leave="afterLeave"
      @before-enter="beforeEnter"
      @enter="enter"
      @after-enter="afterEnter"
      @before-leave="beforeLeave"
      @leave="leave">
      <div v-if='transitionFrame === 1' class="question-row next" :class="{'report-row': uiType === 4}">
        <xSpin class="loading-spinner" v-model="loading" />
        <!-- 首页 -->
        <template v-if="uiType === 1">
          <div class="questionnaire">
            <!-- 标题 -->
            <div class="project-title">
              {{questionnaire.title}}
            </div>
            <!-- 说明 -->
            <div class="project-description">
              <span class="description">
                {{questionnaire.description || '为了方便对您的情况进行详细分析，请根据问卷提示填写相关信息，作答结束后，在线为您出具专业法律咨询意见书，供您参考。'}}
              </span>
            </div>
            <div class="bottom-btn">
              <button class="start-btn" @click="start">开始咨询</button>
            </div>
          </div>
        </template>

        <!-- 题目页面 -->
        <template v-if='uiType === 2'>
          <div class="go-last" :class="{'show': showLastBtn}" @click.stop="answer(2)">
            <img src="../assets/last.png">
            <span class="text">上一题</span>
          </div>
          <div class="redo" @mouseover="showLastBtn = false" @mouseout="showLastBtn = true" @click.stop="reset($event);showLastBtn = true">
            <img src="../assets/redo.png">
            <span class="text">重做</span>
          </div>
          <div class="questionnaire" v-if="!report && this.schema">
            <div class="questions">
              <div style="height: 100%;display: flex;flex-direction: column;">
                <h2 class="question-title" style="text-align: center; flex: 0 0 auto">
                  {{ schema.title }}
                </h2>
                <p class="question-description">
                  {{ schema.description }}
                </p>
                <div class="question-options">
                  <template v-for="(key, index) in schema.properties">
                    <Additional v-if="key.additional" :title="key.title" v-model="key.additionalShow" :key="index" :additionalKey="index" @commit="Commit" @have="AdditionalDown">
                      <template v-for="(k, inx) in key.properties">
                        <schemaComponent :name="getComponentName(k)" apiType="public" :propsObject="k" :schemaKey="index + '-----' + inx" @input="Input" @commit="Commit" @additional="Additional" :key="inx" @validator="initValidator" />
                      </template>
                    </Additional>
                    <MultiNest v-else-if="key.uuid === '00000000-0000-0000-0000-000000000012' && key.multiKey" :properties="schema.properties" :answers="answers" :title="key.title" :schemaKey="index" :key="index" :multiKey="key.multiKey" @additional="Additional"/>
                    <schemaComponent v-else="" apiType="public" :name="getComponentName(key)" :propsObject="key" :schemaKey="index" @input="Input" @commit="Commit" @additional="Additional" :key="index" @validator="initValidator"/>
                  </template>
                </div>
              </div>
            </div>
          </div>
        </template>

        <!-- 填写信息页面 -->
        <template v-if="uiType === 3">
          <div class="go-last" :class="{'show': showLastBtn}" @click.stop="answer(2)">
            <img src="../assets/last.png">
            <span class="text">上一题</span>
          </div>
          <div class="redo" @mouseover="showLastBtn = false" @mouseout="showLastBtn = true" @click.stop="reset($event);showLastBtn = true">
            <img src="../assets/redo.png">
            <span class="text">重做</span>
          </div>
          <div class="questionnaire commit-info">
            <div class="title">
              <img class="image" src="../assets/commit.png">
              <h2 class="text">提交成功</h2>
            </div>
            <div class="description">
              您的情况已提交成功，正在为您详细分析，请您填写联系方式方便律师及时与您沟通，如有不便，可不填跳过。
            </div>
            <div class="info-form">
              <xInput v-model="customerInfo.name" placeholder="您的称呼（如：王先生）"/>
              <xInput v-model="customerInfo.phone" placeholder="您的联系方式"/>
              <button class="lp-btn-primary" @click="getReport">提交并查看法律分析报告</button>
            </div>
          </div>
        </template>

        <!-- 报告页面 -->
        <template v-if="uiType === 4">
          <div class="report">
            <div class="report-title">律品法律咨询意见书</div>
            <div class="report-greetings">
              <h1>先生您好：</h1>
              <p>非常感谢您对我们的信任与支持。以下法律意见是根据你填写的信息，并参考上千万份法院裁判文书数据，以及业内资深法官、律师、法律专业工作者的集体智慧生成。供您参考！</p>
            </div>
            <div class="report-content report-md" ref="report" v-html="reportContent">
            </div>
          </div>
          <div class="side-bar">
            <div class="report-nav">
              <h2>报告导航</h2>
              <ul class="nav-list">
                <li class="nav-item" v-for="(item, index) in outlines" :key="index">
                  <a :href="item.anchor">{{index + 1}} {{item.title}}</a>
                </li>
              </ul>
              <ul class="operation-list">
                <li class="operation-item" @click.stop="reset($event);showLastBtn = true">
                  <img src="../assets/download.png">
                  <span class="m-l-15">下载报告</span>
                </li>
                <li class="operation-item">
                  <img src="../assets/download.png">
                  <span class="m-l-15">打印报告</span>
                </li>
                <li class="operation-item">
                  <img src="../assets/download.png">
                  <span class="m-l-15">重做</span>
                </li>
              </ul>
              <div class="qr-code">
                <div class="qr-img">
                  <img src="../assets/QRCODE.png">
                </div>
                <p class="qr-text">
                  扫描二维码在手机中进行查看
                </p>
              </div>
            </div>
          </div>
        </template>
      </div>
    </transition>
  </div>
</div>
</template>

<script>
import question from '@/api/questionOpen'
import Additional from '@components/additional'
import MultiNest from '@components/multiNest'
import schemaComponent from '@components/schemaComponent'
import uuidv4 from 'uuid/v4'
import Tree from '@/components/simple-tree'
import showdown from 'showdown'
const converter = new showdown.Converter()

export default {
  name: 'question-page',
  components: {
    schemaComponent,
    Additional,
    MultiNest,
    Tree
  },
  data () {
    return {
      showLastBtn: true,
      schema: null,
      schemaBack: null,
      schemaString: null,
      onRecord: false,
      questionnaire: {},
      answers: {},
      recordId: null,
      report: false,
      reportContent: null,
      projectId: '3001',
      additionalKeys: [],
      loading: false,
      validators: {},
      transitionName: 'slide-next', // 过度方向 'slide-next': 下一题， 'slide-prev': 上一题
      transitionFrame: 0, // 过度标志，0, 1
      uiType: 1,
      customerInfo: {
        name: '',
        phone: ''
      },
      outlines: []
    }
  },
  computed: {
    token () {
      return this.$store.state.user.token
    }
  },
  watch: {
  },
  async created () {
    this.projectId = this.$route.query.projectId || '1'
    this.rootId = this.$route.query.rootId
    if (this.$route.query.reset == 1) { // eslint-disable-line
      const xAccessToken = uuidv4()
      window.localStorage.xAccessToken = xAccessToken
      this.$request.defaults.headers.common['x-access-token'] = xAccessToken
      window.localStorage.setItem('onRecord_' + this.projectId, '0')
      window.location.href = window.location.href.replace('&reset=1', '')
    }
    if (window.localStorage.getItem('onRecord_' + this.projectId) === '3') {
      await this.getReport()
      // this.requestAnswerRecord()
    } else if (window.localStorage.getItem('onRecord_' + this.projectId) === '1') {
      await this.answer(1)
    } else {
      await this.answer(null, '', '')
    }
    this.$bus.$on('get-root-project', (callback) => callback(this.rootId))
  },
  beforeDestroy () {
    this.$bus.$off('get-root-project')
  },
  methods: {
    answerHandler (answer, schema) {
      const uuids = ['00000000-0000-0000-0000-000000000013', '00000000-0000-0000-0000-000000000005', '00000000-0000-0000-0000-000000000007'] // 判断、单选、多选
      let r = this.deepCopy(answer)
      for (const key in schema.properties) {
        if (uuids.includes(schema.properties[key].uuid)) {
          for (const n in schema.properties[key].properties) { // TODO: 一堆脏代码
            // console.log(answer[key])
            // console.log(schema.properties[key])
            if (!answer[key].includes(n) && schema.properties[key].properties[n].additionalKeys && schema.properties[key].properties[n].additionalKeys.length) { // eslint-disable-line
              // const addTmpKey = schema.properties[key].properties[n].additionalKeys[0]
              // console.log(schema.properties[addTmpKey])
              // console.log(Object.keys(schema.properties[addTmpKey].properties)[0])
              console.log(Object.keys(schema.properties[schema.properties[key].properties[n].additionalKeys[0]].properties)[0])
              delete r[Object.keys(schema.properties[schema.properties[key].properties[n].additionalKeys[0]].properties)[0]]
              console.log(r)
            }
          }
        }
      }
      return JSON.stringify(r)
    },
    initValidator (e) {
      this.validators[e.key] = e.validator
    },
    reset () {
      const xAccessToken = uuidv4()
      window.localStorage.xAccessToken = xAccessToken
      this.$request.defaults.headers.common['x-access-token'] = xAccessToken
      window.localStorage.setItem('onRecord_' + this.projectId, '0')
      this.answer(null, 0, 0)
    },
    async answer (action = 1, schema, valueJson, jumpToQuestion) {
      if (this.loading) return
      this.loading = true
      this.schemaBack = null
      if (this.answers.button === null) {
        delete this.answers.button
      }
      let data = await question.answer.apply(this, [{
        projectId: this.projectId,
        action, // 1 为下一题，2 为上一题或者跳到做题记录某一题
        targetId: jumpToQuestion || null, // 跳转到某题目的记录id
        schema: schema === 0 ? null : (action === 2 ? null : this.schema), // 原样返回json模式
        valueJson: valueJson === 0 ? null : (action === 2 || JSON.stringify(this.answers) === '{}' ? null : this.answerHandler(this.answers, this.schema)) // 做题得到的结果
      }])
      this.loading = false

      if (data) {
        if (data.status === 1) {
          console.log(data)

          // 设置过渡动画方向
          await this.setTransition(action === 1 ? 'slide-next' : 'slide-prev')

          this.uiType = data.result.uiType

          // 1返回的是开始咨询界面的json模式,2:联系方式界面json模式,3:做题界面的json模式
          if (data.result.uiType === 1) {
            window.localStorage.setItem('onRecord_' + this.projectId, '0')
            this.questionnaire = data.result.schema
            this.report = false
            this.onRecord = false
          } else if (data.result.uiType === 2) {
            window.localStorage.setItem('onRecord_' + this.projectId, '1')
            let schema = data.result.schema
            this.additionalKeys = null
            this.schemaString = JSON.stringify(schema)
            this.validators = {}
            this.schema = schema
            // const valueJson = JSON.stringify(this.answers)
            this.answers = this.JsonSchemaParse(this.schema)
            this.report = false
            if (schema.properties === null) {
              this.onRecord = false
            } else {
              this.onRecord = true
              // window.sessionStorage.valueJson = valueJson
              // window.sessionStorage.recordId = this.recordId
              // window.sessionStorage.schema = JSON.stringify(this.schema)
            }
          } else if (data.result.uiType === 3) { // TODO: 联系方式界面
            window.localStorage.setItem('onRecord_' + this.projectId, '1')
          }
        } else {
          this.$Message.error(data.msg || '未知错误')
        }
      } else {
        this.$Message.error('发生错误，请稍后再试')
      }
    },
    async getReport () {
      let reportData = await question.getReport.apply(this, [{ ...this.customerInfo, projectId: this.projectId }])

      // 设置过渡动画方向
      await this.setTransition('slide-next')

      this.uiType = 4

      if (reportData.status === 1) {
        console.log(reportData)
        const convertedReport = converter.makeHtml(reportData.result.remark)
        this.reportContent = convertedReport
        this.report = true
        this.onRecord = true
        this.schemaBack = JSON.stringify(this.schema)
        this.schema = null // 防止做题高亮显示在最后一题
        window.localStorage.setItem('onRecord_' + this.projectId, '3')

        this.$nextTick(() => {
          const el = this.$refs.report
          if (!el) return
          const h1s = el.querySelectorAll('h1')
          this.outlines = [...h1s].map(item => {
            item.id = item.id ? item.id : uuidv4()
            return {
              title: item.innerText,
              anchor: '#' + item.id
            }
          })
          console.log(this.outlines)
        })
      } else {
        this.$Message.error(reportData.msg)
      }
    },
    reportAnswer (val) {
      this.schema = JSON.parse(this.schemaBack)
      this.answer(val)
    },
    typeToValue (type) {
      switch (type) {
        case 'number':
          return null
        case 'string':
          return ''
        case 'boolean':
          return false
        case 'array':
          return []
        case 'object':
          return {}
        case 'whitespace':
          return ' '
        case 'null':
          return null
        default:
          return null
      }
    },
    JsonSchemaParse (json) {
      let tmp = {}
      for (const key in json.properties) {
        if (json.properties[key].additional) { // 追加题平铺
          for (const k in json.properties[key].properties) {
            tmp[k] = this.typeToValue(json.properties[key]['properties'][k]['type'])
          }
        } else {
          // if (!(json.properties[key].uuid === '00000000-0000-0000-0000-000000000012' && json.properties[key].multiKey) && json.properties[key].uuid !== '00000000-0000-0000-0000-000000000024') {
          if (!(json.properties[key].uuid === '00000000-0000-0000-0000-000000000012' && json.properties[key].multiKey)) {
            tmp[key] = this.typeToValue(json.properties[key]['type'])
          }
        }
      }
      console.log(tmp)
      return tmp
    },
    async start () {
      await this.answer(1, 0, 0)
    },
    getComponentName (key) {
      return key.uuid
    },
    Input (val) {
      console.log(val.schemaKey)
      // this.$set(this.values, Object.keys(val)[0], val[Object.keys(val)[0]])
      if (val.schemaKey.indexOf('-----') !== -1) {
        // if (this.answers[val.schemaKey.split('-----')[0]]) {
        //   this.$set(this.answers[val.schemaKey.split('-----')[0]], val.schemaKey.split('-----')[1], val.value)
        // } else {
        //   this.$set(this.answers, val.schemaKey.split('-----')[0], {})
        //   this.$set(this.answers[val.schemaKey.split('-----')[0]], val.schemaKey.split('-----')[1], val.value)
        // }
        this.$set(this.answers, val.schemaKey.split('-----')[1], val.value)
      } else {
        this.$set(this.answers, val.schemaKey, val.value)
      }
      console.log(this.answers)
    },
    Commit (val) { // eslint-disable-next-line
      if (this.validators[val.schemaKey] && typeof this.validators[val.schemaKey] === 'function') {
        this.validators[val.schemaKey]().then(() => {
          this.commitAnswer(val)
        }, () => {
          this.$Message.error(`请检查必填项！`)
        })
      } else if (val.schemaKey === 'button') {
        const validates = []
        for (let key in this.validators) {
          validates.push(this.validators[key]())
        }
        Promise.all(validates).then(() => {
          this.commitAnswer(val)
        }).catch(err => {
          console.log(err)
          this.$Message.error(`请检查必填项！`)
        })
      } else {
        this.commitAnswer(val)
      }
    },
    commitAnswer (val) {
      this.$nextTick(() => {
      })
      if (val.value) {
        this.$set(this.answers, val.schemaKey, val.value)
      }
      // console.log(val.additionalKeys)
      if (this.additionalKeys && this.additionalKeys.length) {
        this.schema.properties[this.additionalKeys[0]].additionalShow = true
      } else {
        this.answer()
      }
    },
    Additional (val) {
      if (val.additionalKeys && val.additionalKeys.length) {
        // if (!this.additionalKeys || (this.additionalKeys && this.additionalKeys.length === 0)) {
        this.additionalKeys = this.deepCopy(val.additionalKeys)
        // }

        if (val.allowClose) { // 特殊用法，请勿效仿
          this.schema.properties[this.additionalKeys[0]].additionalShow = true
        } else {
          this.schema.properties[this.additionalKeys[0]].additionalShow = 2
        }
      }
    },
    AdditionalDown (key) { // eslint-disable-next-line
      for (const n in this.additionalKeys) {
        if (this.additionalKeys[n] === key) {
          this.additionalKeys.splice(n, 1)
          if (this.additionalKeys && this.additionalKeys.length) {
            this.schema.properties[this.additionalKeys[0]].additionalShow = true
          }
          return
        }
      }
    },
    backTo (name) {
      if (name === 'Report') {
        this.$router.push({ name, query: { projectId: (this.$route.query.projectId || '3001'), rootId: (this.$route.query.rootId || '3001') } })
      } else if (name === 'Page') {
        this.$router.push({ name, params: { projectId: this.$route.query.rootId } })
      } else {
        this.$router.push({ name, query: { projectId: (this.$route.query.rootId || '3001') } })
      }
    },
    async setTransition (type) {
      this.transitionName = type
      return new Promise((resolve, reject) => {
        this.$nextTick(() => {
          this.transitionFrame = (this.transitionFrame + 1) % 2
          resolve()
        })
      })
    },
    afterLeave () {
      console.log('%cafter leave', 'color: red; font-weight: bold')
    },
    beforeEnter () {
      console.log('%cbefore enter', 'color: red; font-weight: bold')
    },
    afterEnter () {
      console.log('%cafter enter', 'color: red; font-weight: bold')
    },
    enter () {
      console.log('%center', 'color: red; font-weight: bold')
    },
    beforeLeave () {
      console.log('%cbefore leave', 'color: red; font-weight: bold')
    },
    leave () {
      console.log('%cleave', 'color: red; font-weight: bold')
    }
  }
}
</script>

